import React from "react"
import { RichText } from "../common/rich-text.component"
import { Section } from "../common/section.component"
import { Image } from "../common/image.component"
import { CustomLink } from "../common/custom-link.component"
import { getLinkClass } from "../../utils/styles"
import { Preview } from "../preview.component"


export const Testimonials = ({ slice, large, index }) => {

  const { primary, fields, label } = slice
  const {anchor} = primary;
  const backgroundColor = primary && primary.bgColor
  const backgroundImage = primary && primary.bgImage
  return (
    <Section backgroundImage={backgroundImage} backgroundColor={backgroundColor} large={true}>
      <div id={anchor}/>
      <div className={`testimonials-slice ${label ? "testimonials-slice-" + label : ""}`}>
        <div className="text-center mb-5">
          <RichText render={primary.title} />
          <RichText render={primary.text} />
        </div>
        {(() => {
          switch (label) {
            case "carousel":
              return (
                <div id="testimonials-carousel" className="carousel testimonials-carousel slide" data-ride="carousel">
                  <ol className="carousel-indicators">
                    {fields.map((item, index) => {
                      return (
                        <li data-target="#testimonials-carousel" data-slide-to={index}
                            className={index === 0 ? "active" : ""} />
                      )
                    })}
                  </ol>
                  <div className="carousel-inner">
                    {fields.map((item, index) => {
                      return (
                        <div className={index === 0 ? " carousel-item active" : "carousel-item"}>
                          <Image image={item.image} />
                          <div className="slider__wrap"></div>
                          <div className="carousel-caption">
                            <div>
                              <Image className="client-photo" image={item.clientPhoto} />
                              <>
                                <p className="client-name">{item.clientName}</p>
                                <div className="d-flex mb-5 rates-wrapper">
                                  {
                                    new Array(5).fill(1).map((i, index) => {
                                      return (<div className={`rating ${index < item.rating ? "active" : ""}`}></div>)
                                    })
                                  }
                                </div>
                              </>
                            </div>
                            <div className="mb-5 text-center">
                              <RichText render={item.text} />
                            </div>
                            <div className="mb-5 text-center">
                              <CustomLink
                                link={item.link}
                                className={getLinkClass(item.link_style, "link")}>
                                {item.link_text}
                              </CustomLink>
                            </div>
                          </div>
                        </div>
                      )
                    })}
                  </div>
                  <a className="carousel-control-prev" href={`#testimonials-carousel`} role="button" data-slide="prev">
                    <div className="carousel-control-bg prev">
                      <span className="carousel-control-icon carousel-control-prev-icon" aria-hidden="true" />
                      <span className="sr-only">Previous</span>
                    </div>
                  </a>
                  <a className="carousel-control-next" href={`#testimonials-carousel`} role="button" data-slide="next">
                    <div className="carousel-control-bg next">
                      <span className="carousel-control-icon carousel-control-next-icon" aria-hidden="true" />
                      <span className="sr-only">Next</span>
                    </div>
                  </a>
                </div>)
            default:
              return (<div className="d-flex flex-wrap">
                {fields.map((item, i) => {
                  return (
                    <div className="col-xs-12 col-md-6 col-lg-4">
                      <div className="col-3-image testimonials-col-3-image mb-3">
                        <Preview image={item.image} videoUrl={item.videoUrl} />
                      </div>
                      <div className="d-flex mb-3">
                        <Image className="client-photo" image={item.clientPhoto} />
                        <div>
                          <p className="client-name">{item.clientName}</p>
                          <div className="d-flex">
                            {
                              new Array(5).fill(1).map((i, index) => {
                                return (<div
                                  className={`rating ${index < item.rating ? "active" : ""}`}></div>)
                              })
                            }
                          </div>
                        </div>
                      </div>
                      <div className="mb-5 text-left">
                        <RichText render={item.text} />
                      </div>
                      <div className="mb-5 text-left">
                        <CustomLink
                          link={item.link}
                          className={getLinkClass(item.linkStyle, "link")}>
                          {item.linkText}
                        </CustomLink>
                      </div>
                    </div>
                  )
                })}
              </div>)
          }
        })()
        }

      </div>
    </Section>
  )
}



