import React, { useCallback, useEffect, useRef, useState } from "react"
import {v4} from "uuid"
import { Image } from "./common/image.component"
import $ from "jquery";
import Carousel, { Modal, ModalGateway } from "react-images"

export const Preview = ({ image, videoUrl }) => {
  const uid = v4()
  const video = useRef(null);

  const onPlay = () => {
    if(video &&  video.current) {
      video.current.play();
    }
  }
  const onStop = () => {
    if(video &&  video.current) {
      video.current.pause();
    }
  }

  const [viewerIsOpen, setViewerIsOpen] = useState(false);

  const openLightbox =() => {
    setViewerIsOpen(true);
  };

  const closeLightbox = () => {
    setViewerIsOpen(false);
  };

  return (
    <>
      <a onClick={openLightbox} className={videoUrl ? "" : `gallery-link`}
         data-toggle="modal" href={`#gallery-${uid}`}>
        <Image className="testimonials-img" image={image}/>
        {videoUrl ? <div className="play-button"/> : null}
      </a>
      <ModalGateway>
        {viewerIsOpen ? (
          <Modal onClose={closeLightbox}>
            <div>
            <a onClick={closeLightbox} style={{position: 'fixed', right: 0}}>
              <svg role="presentation" viewBox="0 0 24 24" className="css-9s8aw7">
                <path fill="white"
                  d="M18.984 6.422l-5.578 5.578 5.578 5.578-1.406 1.406-5.578-5.578-5.578 5.578-1.406-1.406 5.578-5.578-5.578-5.578 1.406-1.406 5.578 5.578 5.578-5.578z"></path>
              </svg>
            </a>
              {videoUrl
                ? (<video controls src={videoUrl} width="100%"/>)
                : <Image image={image}/>
              }
            </div>
          </Modal>
        ) : null}
      </ModalGateway>
    </>
  )
}
